.logged_in {
  .flash {
  	display: block; position: fixed; padding: 1em 2em; bottom: 3.125em; right: 3.125em;
    transition: 500ms;
    opacity: 1;

    &_notice {
      background-color: var(--kubik-interface-color-notice);
      color: white;
    }

    &_alert {
      background-color: var(--kubik-interface-color-alert);
      color: white;
    }
    &.hide {
      opacity: 0;
    }
  }
}
